.store-card-ctn {
	position: relative;
	display: flex;
	flex-direction: column;
	min-width: 350px;
	height: 340px;
	overflow: hidden;
	border-radius: 5px;
	background-color: #fff;
	font-family: Proxima Nova, -apple-system, BlinkMacSystemFont, "Segoe UI",
		Roboto, Oxygen, Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
	cursor: pointer;
	text-decoration: none;
	box-shadow: 0 1px 1px 1px #0000001c;

	&:hover {
		box-shadow: 0px 6px 20px -1px #00000030,	0 1px 1px 1px #0000001c;
		;
	}
	&:not(:last-child) {
		margin-right: 15px;
	}
}

.store-card-banner {
	width: 100%;
	height: 100px;
	object-fit: cover;
	margin-bottom: -40px;
}

.store-card-info {
	text-align: center;
}

.store-card-logo {
	height: 80px;
	width: 80px;
	border-radius: 5px;
	box-shadow: 0 2px 15px 0 #00000033;
}

.store-card-name {
	margin: 20px 0;
	font-size: 24px;
	font-weight: 600;
	color: #333333;
}

.store-card-products > img {
	height: 70px;
	width: 70px;
	border-radius: 8px;
	border: 1px solid #eee;
}
.store-card-products > img:not(:last-child) {
	margin-right: 10px;
}

.store-card-link {
	margin-top: 5px;
	font-weight: 600;
	color: #999;
}

.store-card-ctn:hover > .store-card-info > .store-card-link {
	color: #468efa;
}

@media only screen and (max-width: 1000px) {
	.store-card-ctn {
		height: 181px;
	}
	.store-card-banner {
		min-height: 124px;
	}
	.store-card-logo {
		position: absolute;
		left: 30px;
	}
	.store-card-name {
		position: relative;
		top: 35px;
		font-size: 16px;
	}
	.store-card-link {
		display: none;
	}
	.store-card-products {
		display: none;
	}
}
