body {
  min-height: 100vh;
  background-color: #ebebeb;
}

*,
*::after,
*::before {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

ul,
ol {
  list-style: none;
}

#root {
  display: flex;
  flex-direction: column;
  width: 100%;
  min-height: 100vh;
  height: 100%;
}

::-webkit-scrollbar {
  width: 4px;
}

::-webkit-scrollbar-track {
  background-color: rgba(206, 203, 203, 0.761);
}

::-webkit-scrollbar-thumb {
  border-radius: 2px;
  background-color: rgba(150, 150, 150, 0.761);
}
